body {
  background-color: black;
}
/* defining grid layout */
.col-main {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(13rem, 1fr));
    margin:  0 21% 0 21%;    
    justify-items: center;   
    grid-gap: 25px;
    padding-top: 10px;
}

/*smaller margin on smaller screen */
@media only screen and (max-width: 1200px) {
  .col-main {
    margin: 0 5% 0 5%;
    grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
  }
}

@media only screen and (max-width: 724px) {
  .col-main {
    margin: 0 5% 0 5%;
    grid-template-columns: repeat(auto-fit, minmax(7rem, 1fr));
  }
}
/*image set to 100% */
  .col-1 img{    
    width: 100%;
    height: auto;
  }  

  /* text format on menu */
  .col-1 p{
    color:#ffb800;
    font-weight: 500;
    font-style: italic;
    line-height: 1.6;
    font-size: 1rem;
    font-family: Montserrat,sans-serif;
  }

  .landing {
    text-align: left;
    margin:  0 21% 0 21%;  
  }

  .landing img {
    width: 316px;
    height: auto; 
  }

  .nav {
    display: flex;
    background-color: #ffb800;
    height: 50px;
    justify-content: flex-start;
  }

  .nav-items {
    margin:  0 21% 0 21%;  
  }

  @media only screen and (max-width: 1200px) {
    .nav-items {
      margin: 0 5% 0 5%;
    }
  }


  @media only screen and (max-width: 1200px) {
    .landing {
      margin: 0 5% 0 5%;
    }
  }

  @media only screen and (max-width: 1200px) {
    .landing img {
      width: 250px;
      height: 25vh;
    }
  }

